.modal {
    position: fixed; /* Фиксированное позиционирование */
    top: 0;
    left: 0;
    width: 100vw; /* Покрываем весь экран */
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5); /* Полупрозрачный черный фон */
    display: flex; /* Используем flexbox для выравнивания */
    align-items: center; /* Вертикальное выравнивание */
    justify-content: center; /* Горизонтальное выравнивание */
    z-index: 1000; /* Поверх всех других элементов */
    
    &__block {
      background-color: #fff; /* Белый фон модального окна */
      padding: 20px; /* Отступы внутри модального окна */
      border-radius: 10px; /* Скругленные углы */
      box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Легкая тень */
      width: 90%; /* Ширина модального окна */
      max-width: 500px; /* Максимальная ширина */
      text-align: center; /* Центрирование текста */
      animation: fadeIn 0.3s ease-in-out; /* Анимация появления */
      max-height: 90vh; /* Ограничиваем высоту модального окна */
      overflow-y: auto; /* Добавляем прокрутку по вертикали, если контент выходит за пределы */
    }
    &__type {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        flex-direction: row;
        margin-bottom: 20px;
        &Add{
          width: 40px;
          height: 40px;
          display: flex;
          align-items: center;
          margin-left:20px;
            margin-bottom: 10px;
          justify-content: center;
          background-color: white;
          border: 1px solid black;

          border-radius: 30px;
        }
        &Block {
          border: 1px solid black;
            background-color: white;
            padding: 10px;
            margin-right:10px;
            margin-bottom: 10px;
            border-radius: 10px;
            cursor: pointer;
        }
    }
   

    &__title {
       border: 1px solid black;
       border-radius: 10px;
       padding: 10px;
       margin-bottom: 20px;
        &Text {
        
        }
    }
    &__btns {
        display: flex;
        justify-content: space-between;
        align-items: center;
        &Btn{
            color: #fff;
            width: 50%;
            background-color: white;
            border: 1px solid rgb(0, 0, 0);
            height: 50px;
            margin:10px;
            border-radius: 20px;
        }
    }

    &__info {
        border: 1px solid rgb(0, 0, 0);
        border-radius: 10px;
        padding: 10px;
        margin-bottom: 20px;
        width: 100%;
        &Text {
          width: 100%; /* Ширина, по необходимости */
         height: auto;
          font-size: 16px;
          line-height: 1.5;
          resize: vertical;          
        }
    }
    &__add {
        background-color: #77EA73;
    }
    &__close {
        background-color: rgb(192, 28, 28);
    }

  
    /* Анимация появления */
    
  }